@import url("https://fonts.googleapis.com/css?family=Open+Sans");
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title], dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input, select {
  vertical-align: middle;
}

.loader {
  border: 16px solid #f3f3f3;
  /* Light grey */
  border-top: 16px solid #3498db;
  /* Blue */
  border-radius: 50%;
  width: 60px;
  height: 60px;
  -webkit-animation: spin 0.6s linear infinite;
          animation: spin 0.6s linear infinite;
  margin: 150px auto;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.pt-toast-message {
  color: #000;
  text-align: center;
  padding: 20px;
}

.pt-intent-danger {
  background-color: #fff;
  border-radius: 5px;
  border: 1px solid #666;
  border-left: 8px solid #D9534F;
  position: absolute;
  width: 90%;
  max-width: 380px;
  opacity: 0;
  left: 50%;
  height: 80px;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  -webkit-animation: appear 100ms forwards;
          animation: appear 100ms forwards;
}

.pt-intent-danger .pt-button {
  background: none;
  border: none;
  border-radius: 50%;
}

.pt-intent-danger .pt-button:before {
  font-family: FontAwesome;
  content: "\f05e";
  color: #000;
  font-size: 1.7em;
  padding: 0 3px 2px;
}

.pt-intent-warning {
  background-color: #fff;
  border-radius: 5px;
  border: 1px solid #666;
  border-left: 8px solid #F0AD4E;
  position: absolute;
  width: 90%;
  max-width: 380px;
  opacity: 0;
  left: 50%;
  height: 80px;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  -webkit-animation: appear 100ms forwards;
          animation: appear 100ms forwards;
}

.pt-intent-warning .pt-button {
  background: none;
  border: none;
  border-radius: 50%;
}

.pt-intent-warning .pt-button:before {
  font-family: FontAwesome;
  content: "\f071";
  color: #000;
  font-size: 1.7em;
  padding: 0 3px 2px;
}

@-webkit-keyframes appear {
  from {
    top: 0px;
    opacity: 0;
  }
  to {
    top: 40px;
    opacity: 1;
  }
}

@keyframes appear {
  from {
    top: 0px;
    opacity: 0;
  }
  to {
    top: 40px;
    opacity: 1;
  }
}

#logo img {
  max-width: 60px;
  -webkit-transition: -webkit-transform 200ms;
  transition: -webkit-transform 200ms;
  -o-transition: transform 200ms;
  transition: transform 200ms;
  transition: transform 200ms, -webkit-transform 200ms;
}

#logo img:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}

* {
  font-family: 'Open Sans';
}

header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 100%;
  border-bottom: 1px solid #666;
}

header .title {
  display: inline-block;
  padding: 30px;
}

header .title h1 {
  font-size: 2em;
}

header .user-control .fa-sign-out {
  padding: 30px;
  color: #000;
  font-size: 2em;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  transition: 200ms;
}

header .user-control .fa-sign-out:hover {
  color: #D9534F;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}

header .user-control .log-in {
  display: block;
  border-radius: 5px;
  margin: 40px;
  padding: 10px;
  color: #000;
  text-decoration: none;
  border: 1px solid #ccc;
  background-color: #389638;
  color: #fff;
  -webkit-transition: background-color 200ms;
  -o-transition: background-color 200ms;
  transition: background-color 200ms;
}

header .user-control .log-in:hover {
  background-color: rgba(56, 150, 56, 0.8);
}

.login-container {
  min-height: 80vh;
  width: 90%;
  max-width: 315px;
  margin: 40px auto 10px auto;
}

.login-container #facebook-login {
  border-radius: 5px;
  border: none;
  background-color: #4267B2;
  color: #fff;
  width: 100%;
  padding: 10px;
  font-size: 1em;
  cursor: pointer;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  transition: 200ms;
}

.login-container #facebook-login:hover {
  background-color: cornflowerblue;
}

.login-container #facebook-login i {
  font-size: 1.5em;
  margin: 0 20px;
  float: left;
}

.login-container .info-box {
  border: 1px solid #ccc;
  display: -ms-flexbox;
  display: flex;
  border-radius: 5px;
  -ms-flex-align: center;
      align-items: center;
  border: 2px solid #5BC0DE;
  padding: 20px;
}

.login-container .info-box h2 {
  font-size: 0.9em;
  font-weight: 600;
}

.login-container .info-box i {
  font-size: 1.4em;
  margin-right: 10px;
}

.login-container label {
  display: block;
  margin-top: 15px;
  font-size: 1.1em;
}

.login-container label input {
  border-radius: 5px;
  width: 100%;
  font-size: 1em;
  outline: none;
  padding: 3px 5px 3px 5px;
  margin-top: 5px;
  border: 1px solid #ccc;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  transition: 200ms;
  -webkit-box-sizing: border-box;
  /* Safari/Chrome, other WebKit */
  /* Firefox, other Gecko */
  box-sizing: border-box;
  /* Opera/IE 8+ */
}

.login-container label input:focus {
  border: 1px solid #000;
}

.login-container input[type="submit"] {
  border-radius: 5px;
  width: 100%;
  margin-top: 15px;
  border: none;
  font-size: 1em;
  background-color: #1D9C73;
  color: #fff;
  padding: 5px;
  cursor: pointer;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  transition: 200ms;
}

.login-container input[type="submit"]:hover {
  background-color: rgba(29, 156, 115, 0.7);
}

section {
  height: 100vh;
}

section h1 {
  padding-top: 30px;
  text-align: center;
  font-size: 1.8em;
}

section p {
  text-align: center;
  font-size: 1em;
  padding-top: 30px;
}

section img {
  width: 90%;
  height: auto;
  border-radius: 20px;
  max-width: 600px;
  display: block;
  margin: 50px auto;
}

footer {
  height: 50px;
  position: relative;
  bottom: 0px;
  width: 100%;
  background: #d4d4d4;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}


